import Helper from '@/function.js';
import loadMore from '../../components/load-more.vue';
export default {

	data() {

		return {
			token: '',
			state: '', //null-全部 0-待审核 1-成功 2-失败
			pageIndex: 1, //第几页
			pageSize: 10, //每页多少行
			sendType: -1,
			recordList: [],
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			total: -1,
			nodata: false
		}
	},
	onShow: function() {
		this.pageIndex = 1;
		this.recordList = [];
		this.selType = -1;
		this.token = uni.getStorageSync('token');
		this.userInfo = uni.getStorageSync('userInfo');
		this.getMoneyOrdersList();
	},

	methods: {
		btnClick: function(index) {
			console.log(index);
			if (this.sendType == index) {
				return;
			}

			this.sendType = index;
			this.getMoneyOrdersList();
		},
		/* 体现记录的列表 */
		getMoneyOrdersList() {
			var _this = this
			var params = {
				state: _this.sendType, //null-全部 0-待审核 1-成功 2-失败
				pageIndex: _this.pageIndex, //第几页
				pageSize: _this.pageSize, //每页多少行

			};
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/MemberGetMoneyOrdersList',
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res + '111');
					if (Helper.CheckData(res)) {
						let {
							errorCode,
							errorMessage,
							list,
							total,
							totalPage
						} = res;
						if (list == null) {
							_this.total = res.data.total;
						}
						_this.recordList = _this.recordList.concat(res.data.list);
						_this.recordList = res.data.list;
						console.log(_this.recordList);
						// _this.setListStatus();
					} else {
						_this.total = true;
					}


				},

				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}
			});




		}


	}

}
